@import url(https://fonts.googleapis.com/css2?family=Neuton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color:#101010;
  color: #aaa;
  font-family: 'Neuton', serif;
  letter-spacing: 3px;
}
.App-link {
  color: #61dafb;
}

a{
  text-decoration: none;
  color: inherit;
}
p{
  margin: 0;
}
.moodBody li{
  margin: 10px 30% 0 0;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes float{
  from{
      -webkit-transform: translate(0);
              transform: translate(0);
  }
  to{
      -webkit-transform: translate(-20px, -10px);
              transform: translate(-20px, -10px);
  }
}
@keyframes float{
  from{
      -webkit-transform: translate(0);
              transform: translate(0);
  }
  to{
      -webkit-transform: translate(-20px, -10px);
              transform: translate(-20px, -10px);
  }
}


.home{
    display: grid;
    grid-template-columns: 60% 30%;
    height: 100vh;
    padding: 50px 20px;
}
.homeLeft{
    text-align: left;
}
.homeRight{
    background: linear-gradient(90deg,rgba(10,10,10,0.1),rgba(10,10,10,0.2)), url("https://i.pinimg.com/564x/24/94/e3/2494e3f8fb1ef27c58f59873eeb11266.jpg");
    background-size: cover;
    margin: 10px;
    background-position: center bottom;
    background-repeat: no-repeat;
    border-radius: 5%;
    animation: float 1s alternate-reverse infinite linear;
    transition: 300ms;
}

.homeLeft h1, p{
    display: inline-block;
    position: absolute;
    left: 0;
    margin: 10px 50px;
    letter-spacing: 3px;
}
.name{
    top: 50%;
    font-size: 70px;
    animation: float 4s alternate-reverse infinite linear;
}
.homeLeft > p{
    top: 62%;
    padding: 0;
    font-size: 30px;
    color: transparent;
    background: #4568DC;
    background: linear-gradient(0deg, #B06AB3, #4568DC);
    -webkit-background-clip: text;
}

@-webkit-keyframes float{
    from{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    to{
        -webkit-transform: translate(-20px, -10px);
                transform: translate(-20px, -10px);
    }
}

@keyframes float{
    from{
        -webkit-transform: translate(0);
                transform: translate(0);
    }
    to{
        -webkit-transform: translate(-20px, -10px);
                transform: translate(-20px, -10px);
    }
}
@media only screen and  (min-widht: 600px){
    .home{
      display: none;
    }
  }
