.workBody{
    margin-top: 30vh;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
}
.workBody h1{
    font-size: 80px;
    position: absolute;
    top: 10px;
    left: 40px;
}
.workTab{
    display: grid;
    grid-template-columns: 70% 30%;
    min-height: 90vh;
    position: relative;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    margin: 20px 40px;
    z-index: 2;
}
.workRight{
    background: url(/portfolio-ver-1.0%20/static/media/NormalisBoring.a106bc58.jpg);
    background-size: cover;
    background-position: bottom;
    border-radius: 5%;
    position: sticky;
    top: 10px;
}
.workCard{
    display: inline-flex;
    flex-direction: column;
    border-radius: 10px;
    border: 5px solid #aaa;
    padding: 10px 20px;
    font-size: 120%;
    cursor: pointer;
    transition: 300ms;
    margin: 10px 20px;
    background-color: rgba(10,10,10,0.3);
}
.workCard:hover {
    background-color: #aaa;
    color: #101010;
    -webkit-transform: scale(1.1,1.1);
            transform: scale(1.1,1.1);
    /* border-color: #101010;
    background: linear-gradient(30deg, #B06AB3, #4568DC); */
}

.socialBody{
    min-height: 120vh;
    padding: 50px 50px 100px 50px;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 25% 25% 25% 25%;
}
.socialHeader{
    margin: 0;
    padding: 0;
    animation: float 2s alternate-reverse infinite linear;
    font-size: 60px;
}
.socialIconBox{
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 25% 20% 30% 25%;
    text-align: center;
    justify-items: center;
    justify-self: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 60%;
}
.socialIcons{
    height: 100px;
    width: 100px;
    background-size: cover;
    /* animation: float 2s alternate-reverse infinite linear; */
    cursor: pointer;
    transition: 300ms;
}
.socialIcons:hover{
    -webkit-transform: scale(1.2,1.2);
            transform: scale(1.2,1.2);
}
